    padding: 0;
    box-sizing: border-box;
} */

/* Navbar */
html {
    scroll-behavior: smooth;
    overflow-x: hidden;
}

.row {
    display: flex;
    flex-direction: row;
}

.column {
    display: flex;
    flex-direction: column;
}


.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 192px 1.6px 192px;
    position: absolute;
    z-index: 1;
    width: 100vw;
    background-color: #285E4D;
}

.navbar-sticky {
    background: #285E4D;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 0.5px 0.5px 0.5px #e5e5e5;
    animation: moveDown 0.5s ease-in-out;
}

.navbar--logo {
    width: 128px;
    height: 32px;
    cursor: pointer;
    /* animation: rotate 0.7s ease-in-out 0.5s; */
}

.navbar--link {
    display: flex;
    list-style: none;
    align-items: center;

}

.navbar--link-item {
    margin: 0 0 0 45px;
    padding: 0 4px;
    color: #C2DC55;
    cursor: pointer;
    font-size: 14px;
}

@keyframes moveDown {
    from {
        transform: translateY(-5rem);
    }

    to {
        transform: translateY(0rem);
    }
}

@keyframes rotate {
    0% {
        transform: rotateY(360deg);
    }

    100% {
        transform: rotateY(0rem);
    }
}


.mxContainerData {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px 192px 0px 192px;
    height: 100vh;
    width: 100vw;
}

.mxButtonHeader {
    border: 1px solid #C2DC55;
    border-radius: 0.7rem;
    font-size: 12px;
    font-weight: 500;
    background-color: #C2DC55;
    color: #285E4D;
    padding: 12px 28px 12px 28px;
}

.mxButton {
    border: 1px solid #C2DC55;
    border-radius: 12px;
    font-size: 14px;
    font-weight: 500;
    line-height: 17.5px;
    letter-spacing: 0.21px;
    background-color: #C2DC55;
    color: #285E4D;
    padding: 16px 32px 16px 32px;
    margin-top: 42px;
}

#button-2 {
    margin-left: 25px;
}

.mxTopHead {
    font-size: 42px;
    font-weight: 700;
    letter-spacing: -1.47px;
    color: #fff;
    line-height: 52.5px;
}

.mxTopSubHead {
    font-size: 21px;
    color: #fff;
    line-height: 32px;
    font-weight: 400;
    /* letter-spacing: 0.315px; */
    margin-top: 24px;
}

#topSubHead {
    max-width: 90%;
}

.mxTopMinHead {
    font-size: 15px;
    color: #fff;
    line-height: 21px;
    margin-top: 32px;
    letter-spacing: 0.3px;
    font-weight: 400;
}

#topMinHead {
    max-width: 66%;
}

.mxHead {
    font-size: 32px;
    font-weight: 500;
    color: #333;
    line-height: 44.8px;
    padding-top: 0;
    letter-spacing: -1.28px;
}

.mxSubHead {
    font-size: 20px;
    font-weight: 400;
    color: #333;
    letter-spacing: 0.6px;
    line-height: 30px;
}

#subHead {
    padding-right: 20px;
}

.mxMinSubHead {
    font-size: 15px;
    color: #6c6c6c;
    line-height: 22.5px;
    letter-spacing: 0.3px;
}

.mxSecTxt {
    font-size: 16px;
    color: #6c6c6c;
    line-height: 22.5px;
}

.mxMinHead {
    font-size: 18px;
    font-weight: 400;
    color: #6c6c6c;
    line-height: 28px;
    margin-top: 10px;
}



.mxLineTxt {
    font-size: 19px;
    font-weight: 400px;
    color: #fff;
}


.mxVideoBox {
    height: 300px;
    background-color: #4F7E4F;
    border-radius: 18.25px;
    width: 75%;
}

.mxEmptyBox {
    height: 450px;
    background-color: #4F7E4F;
    border-radius: 18.25px;
    width: 70%;
    float: right;
}

#otherBox {
    height: 300px;
    border: 1px solid #333;
    background-color: #F0F6D5;
    border-radius: 10;
    width: 100%;
    margin-top: 32px;
}

.wealthBox {
    height: 400px;
    border: 1px solid #F0F6D5;
    border-radius: 25px;
    background-color: #F0F6D5;
    width: 80%;
    /* margin-top: 32px; */
}

.mxPricingCard {
    border: 1px solid #F0F6D5;
    border-radius: 32px;
    background-color: #F0F6D5;
    padding: 28px 24px 28px 24px;
    width: 350px;
    box-shadow: 1px solid #6c6c6c;
}

.para {
    display: flex;
    flex-direction: row;
    padding-left: 25%;
}

#sec3-alignTxt {
    /* margin-top: 25px; */
    text-align: center;
}

#sec3-subTxt {
    text-align: center;
    /* width: 55%; */
    align-self: center;
    padding-top: 8px;
}

.mxBoxes {
    background-color: #F5F5F5;
    border: 1px solid #e5e5e5;
    border-radius: 8px;
    padding: 1px 24px 1px 24px;
    width: 376px;
    height: 240px;
}

.mxBoxesImg {
    height: 100px;
    width: 100px;
    float: left;
}

.mxOptionHead {
    float: left;
    font-size: 20px;
    font-weight: 500;
    line-height: 26.35px;
    letter-spacing: 0.3px;
    color: #333;
}

.mxOptionContent {
    float: left;
    font-size: 15px;
    font-weight: 400;
    line-height: 22.5px;
    /* letter-spacing: 0.47px; */
    color: #6c6c6c;
}

.s-strip {
    color: #F7D749;
    background-color: #285E4D;
    border-radius: 10px;
    padding: 5px 10px 5px 10px;
    font-size: 12px;
    height: 30px;
}

#sec3-boxTxt {
    padding-top: 0;
}

#sec3-boxsubTxt {
    padding-top: 4px;
    max-width: 90%;
}

.mxSubSmallHead {
    font-size: 12px;
    color: #fff;
    line-height: 15.62px;
}

.faqHeadTxt {
    color: #58985F;
    font-size: 20px;
    font-weight: 500;
    /* line-height: 44px; */
    margin-bottom: 10px
}

.mxSubContainer {
    align-content: 'center';
    display: flex;
    margin-top: 24px;
}

.mxhide-scrollbar {
    overflow-x: auto;
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}

.mxhide-scrollbar::-webkit-scrollbar {
    display: none;
    /*chrome and Safari */
}

