@media screen and (max-width: 641px) {

    .mxDesktopHidden {
        display: flex !important;
    }

    .mxDesktopVisible {
        display: none !important;
    }

    .navbar {
        display: flex;
        border-bottom: 0.5px solid #E5E5E5;
        align-items: center;
        flex-direction: row;
        justify-content: space-between;
        padding: 10px 20px 0px 20px;
        position: absolute;
        z-index: 1;
        width: 100%;
    }

    .navbar-sticky {
        background: #285E4D;
        position: fixed;
        top: 0;
        left: 0;
        box-shadow: 0.5px 0.5px 0.5px #222;
        animation: moveDown 0.5s ease-in-out;
        z-index: 21;
    }

    .navbar--logo {
        width: 87px;
        height: 20px;
        /* animation: rotate 0.7s ease-in-out 0.5s; */
    }

    .nav-menu {
        display: flex;
        list-style: none;
        align-items: center;
        margin-right: 35px;
    }

    .nav-menu-item {
        margin: 5px 0 0 0;
        padding: 0 10px;
        color: #fff;
        cursor: pointer;
        font-size: 14px;
    }

    .navbar--link {
        display: flex;
        flex-direction: column;
        width: 100%;
        border-top: 1px solid #fff;
        position: absolute;
        top: 52.5px;
        left: -110%;
        opacity: 1;
        transition: all 0.5s ease;
        height: 100vh;
        padding-left: 0%;
    }

    .navbar--link.active {
        background: #fff;
        top: 52.5px;
        left: 0px;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
        height: 100vh;
        padding-left: 0%;
        margin: 0%;
    }

    .navbar--link-item {
        margin: 0 0 0 0;
        padding: 10px 10px;
        color: #333;
        cursor: pointer;
        font-size: 16px;
    }

    .nav-icon {
        display: block;
        position: absolute;
        /* top: 0; */
        right: 0;
        /* font-size: 32px; */
        cursor: pointer;
        color: #fff;
        transform: translate(-70%, -20%);
    }

    @keyframes moveDown {
        from {
            transform: translateY(-5rem);
        }

        to {
            transform: translateY(0rem);
        }
    }

    @keyframes rotate {
        0% {
            transform: rotateY(360deg);
        }

        100% {
            transform: rotateY(0rem);
        }
    }

    .icon-forward {
        transform: rotate(-90deg);
        height: 8px;
    }

    .menuSubTxt {
        font-weight: 500;
        font-size: 14px;
        color: #333;
        line-height: 19.6px;
    }


    /* carosuel custom css  */

    .carousel-control-next-icon,
    .carousel-control-prev-icon {
        display: inline-block;
        width: 1rem;
        height: 2rem;
        background-repeat: no-repeat;
        background-position: 50%;
        background-size: 100% 100%;
    }

    .carousel-control-next,
    .carousel-control-prev {
        position: absolute;
        top: 0;
        bottom: 0;
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 5%;
        padding: 0;
        color: #fff;
        text-align: center;
        background: 0 0;
        border: 0;
        opacity: .5;
        transition: opacity .15s ease;
    }


    .mxContainerData {
        display: flex;
